function PageSettingMain(props) {
  useEffect(() => {
    HConfig.changeTitle("Setting", "Setting")
    return () => {
      HConfig.changeTitle(null, "")
    }
  }, [])

  // console.warn("PageSettingMain: ", HConfig.extSettingParts)
  return (
    <div>
      {/* <Example /> */}
      {HConfig.extSettingParts != null &&
        HConfig.extSettingParts.length > 0 &&
        HConfig.extSettingParts.map((Tag: React.ComponentClass, i) => {
          return <div key={i}>{<Tag />}</div>
        })}
    </div>
  )
}

export default PageSettingMain
